<template>
  <div class="recommend">
    <div class="bg">
      <img src="../../assets/img/pic/bg2.png" alt />
    </div>
    <div class="mid">
      <div class="mid-top">
        <img :src="$utils.dealImg(headimgurl)" alt />
        <p class="name">{{name}}</p>
        <p class="text">邀请好友扫描二维码一起享福利！</p>
      </div>
      <img class="qrCode" :src="pic" alt />
      <div class="btn" @click="shareShow = true">邀请好友</div>
    </div>
    <div class="mask" v-if="shareShow" @click="hideShareMask">
      <img src="../../assets/img/activity/shang.png" alt="">
    </div>
  </div>
</template>
<script>
import { recommend, wxApi } from "@/api";
export default {
  data() {
    return {
      headimgurl: "",
      name: "",
      pic: "",
      shareShow: false
    };
  },
  methods: {
    hideShareMask() {
      this.shareShow = false;
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      recommend({
        url:
          this.$config.http + "/wx/#/register?parentCode=" +
          (sessionStorage.getItem("userId") || '')
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.pic = res.data.qrCode;
        } else {
          this.$toast.fail(res.msg);
        }
      });
    },
    share() {
      let that = this;
      console.log(window.location.href.split("#")[0]);
      wxApi()
        .then(res => {
          console.log(this.$wx, res.data);
          this.$wx.config({
            debug: false,
            appId: res.data.appId,
            timestamp: res.data.timestamp,
            nonceStr: res.data.nonceStr,
            signature: res.data.signature,
            jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"]
          });
          this.$wx.ready(function() {
            that.$wx.onMenuShareAppMessage({
              title: that.$config.name, // 分享标题
              desc:  that.$config.name, // 分享描述
              link:
                 that.$config.http + "/wx/#/beautySalonMallIndex?parentId=" +
                sessionStorage.getItem("userId"), // 分享链接
              imgUrl: that.$config.logo, // 分享图标
              type: "link", // 分享类型,music、video或link，不填默认为link
              dataUrl: "", // 如果type是music或video，则要提供数据链接，默认为空
              success: function() {
                // 用户确认分享后执行的回调函数
                that.shareShow = false;
              },
              cancel: function() {
                // 用户取消分享后执行的回调函数
                that.$toast.fail("取消分享");
              }
            });
            that.$wx.onMenuShareTimeline({
              title:  that.$config.name, // 分享标题
              link:
                that.$config.http + "/wx/#/beautySalonMallIndex?parentId=" +
                sessionStorage.getItem("userId"), // 分享链接
              imgUrl: that.$config.logo, // 分享图标
              success: function() {
                that.shareShow = false;
              },
              cancel: function() {
                // 用户取消分享后执行的回调函数
                that.$toast.fail("取消分享");
              }
            });
          });
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  mounted() {
    document.title = "邀请好友";
    this.headimgurl = sessionStorage.getItem("headimgurl");
    this.name = sessionStorage.getItem("userName");
    this.share();
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
.recommend {
  height: 100%;
  .bg {
    width: 100%;
    height: calc(100% - 92px);
    position: fixed;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  .mid {
    background: url("../../assets/img/pic/bg4.png") no-repeat;
    background-size: 100% 100%;
    position: relative;
    z-index: 1;
    text-align: center;
    margin: 0 30px;
    height: 80%;
    padding: 10vh 0 0;
    top: 30px;
    &-top {
      img {
        width: 60px;
        border-radius: 50%;
      }
      .name {
        font-weight: bold;
        line-height: 30px;
        font-size: 18px;
      }
      .text {
        color: #999;
        font-size: 12px;
        line-height: 25px;
      }
    }
    .qrCode {
      margin: 10vh 0 0;
      width: 55%;
    }
    .btn {
      width: 90%;
      height: 50px;
      line-height: 50px;
      font-size: 13px;
      margin: 20px auto 0;
      background: $base-color;
      color: #fff;
      border-radius: 5px;
    }
  }
  .mask {
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    img {
      position: fixed;
      top: 10px;
      right: 10px;
      width: 100px;
    }
  }
}
</style>
